.progressbar { 
	height: 20px;  /* Can be anything */
	position: relative;
	background: #e7e7e8;
	-moz-border-radius: 25px;
	-webkit-border-radius: 25px;
	border-radius: 25px;
	padding: 10px;
	width: 90%;
    margin: auto;
    margin-bottom: 20px;
	-webkit-box-shadow: inset 0 -1px 1px rgba(255,255,255,0.3);
	-moz-box-shadow   : inset 0 -1px 1px rgba(255,255,255,0.3);
	box-shadow        : inset 0 -1px 1px rgba(255,255,255,0.3);
}
.progressbar > span {
	height: 20px !important;
    display: block;
    bottom: 10px;
    left: -10px;
	display: block;
	height: 100%;
	-webkit-border-top-right-radius: 8px;
	-webkit-border-bottom-right-radius: 8px;
	-moz-border-radius-topright: 8px;
	-moz-border-radius-bottomright: 8px;
	border-top-right-radius: 8px;
	border-bottom-right-radius: 8px;
	-webkit-border-top-left-radius: 20px;
	-webkit-border-bottom-left-radius: 20px;
	-moz-border-radius-topleft: 20px;
	-moz-border-radius-bottomleft: 20px;
	border-top-left-radius: 20px;
	border-bottom-left-radius: 20px;
	background-color: #e1787d;
	-webkit-box-shadow: 0 0px 20px rgba(0, 0, 0, 0.14), 0 0px 20px rgba(0, 0, 0, 0.1);
	-moz-box-shadow: 0 0px 20px rgba(0, 0, 0, 0.14), 0 0px 20px rgba(0, 0, 0, 0.1);
	box-shadow: 0 0px 20px rgba(0, 0, 0, 0.14), 0 0px 20px rgba(0, 0, 0, 0.1);
	position: relative;
	overflow: hidden;
}
.progressbar > span:after {
	content: "";
	position: absolute;
	top: 0; left: 0; bottom: 0; right: 0;
	-webkit-animation: none;
	background-image: none;
	z-index: 1;
	-webkit-background-size: 50px 50px;
	-moz-background-size: 50px 50px;
	-webkit-animation: move 2s linear infinite;
	-webkit-border-top-right-radius: 8px;
	-webkit-border-bottom-right-radius: 8px;
	-moz-border-radius-topright: 8px;
	-moz-border-radius-bottomright: 8px;
	border-top-right-radius: 8px;
	border-bottom-right-radius: 8px;
	-webkit-border-top-left-radius: 20px;
	-webkit-border-bottom-left-radius: 20px;
	-moz-border-radius-topleft: 20px;
	-moz-border-radius-bottomleft: 20px;
	border-top-left-radius: 20px;
	border-bottom-left-radius: 20px;
	overflow: hidden;
}
  
